  dualRenderBattleScreen(): DualRenderOutput {
    const { appWidth, appHeight, screen } = this.state;
    const elapsedSeconds = getElapsedSeconds(
      screen.currentTimestamp,
      screen.animationStartTime
    );

    const battleEvents = getBattleEvents(
      screen.blockEditor.canvas,
      getInitialLevelState(screen.level)
    );
    const battleElapsedSeconds =
      screen.animationKind === AnimationKind.PlayBattle
        ? screen.battleAnimationOffsetSeconds + elapsedSeconds
        : screen.animationKind === AnimationKind.PauseBattle
        ? screen.battleAnimationOffsetSeconds
        : 0;
    const battleAnimationData = getBattleAnimationData(
      getInitialLevelState(screen.level),
      battleEvents,
      battleElapsedSeconds
    );

    const minimapBBox = this.getMinimapBBox();

    const minimapBlockEditorProps: BlockEditorProps<MinimapBlockKind> = {
      config: this.getMinimapBlockEditorConfig(),
      state: this.getMinimapBlockEditorState(),

      measureText: this.measureSvgText,
      setState: noOp,
    };

    const reactElement = (
      <div className="App">
        <div className="AppMain">
          <svg
            className="MainSvg"
            width={this.state.appWidth}
            height={appHeight}
          >
            <g
              transform={`translate(${minimapBBox.topLeft.x} ${
                minimapBBox.topLeft.y
              }) scale(${minimapBBox.dimensions.width / appWidth} ${
                minimapBBox.dimensions.height / appHeight
              })`}
              opacity={battleElapsedSeconds === 0 ? 0 : 1}
            >
              <BlockEditor {...minimapBlockEditorProps} />
            </g>
          </svg>
        </div>
      </div>
    );

    const canvasElement: CanvasElement = {
      kind: CanvasElementKind.Group,
      children: [
        this.renderBattleAndOutcomeScreenCanvasElement(
          -appHeight,
          screen.blockEditor,
          screen.level,
          battleElapsedSeconds,
          battleAnimationData
        ),
        this.renderMinimapHoleCanvasElement(),
        this.renderTutorialCanvasElement(),
        this.renderPuzzleScreenButtonsCanvasElement(),
      ],
    };

    return { reactElement, canvasElement };
  }

  dualRenderNextLevelAnimation(): DualRenderOutput {
    const { appHeight, screen } = this.state;
    const elapsedSeconds = getElapsedSeconds(
      screen.currentTimestamp,
      screen.animationStartTime
    );
    const blockEditorDy = Math.min(
      (appHeight * elapsedSeconds) / SCREEN_TRANSITION_ANIMATION_SECONDS -
        appHeight,
      appHeight
    );

    const prevBattleEvents = getBattleEvents(
      screen.prevLevelBlockEditor.canvas,
      getInitialLevelState(screen.level - 1)
    );
    const prevBattleElapsedSeconds = Number.MAX_SAFE_INTEGER;
    const prevBattleAnimationData = getBattleAnimationData(
      getInitialLevelState(screen.level - 1),
      prevBattleEvents,
      prevBattleElapsedSeconds
    );

    const battleElapsedSeconds = 0;
    const battleEvents: readonly BattleEvent[] = [];
    const battleAnimationData = getBattleAnimationData(
      getInitialLevelState(screen.level),
      battleEvents,
      battleElapsedSeconds
    );

    const blockEditorProps: BlockEditorProps<BlockKind> = {
      config: this.getMainBlockEditorConfig(),
      state: screen.prevLevelBlockEditor,

      measureText: this.measureSvgText,
      setState: noOp,
    };

    const reactElement = (
      <div className="App">
        <div className="AppMain">
          <svg
            className="MainSvg"
            width={this.state.appWidth}
            height={appHeight}
          >
            <g transform={`translate(0 ${blockEditorDy})`}>
              <BlockEditor {...blockEditorProps} />
            </g>
          </svg>
        </div>
      </div>
    );

    const canvasElement: CanvasElement = {
      kind: CanvasElementKind.Group,
      children: [
        this.renderBattleAndOutcomeScreenCanvasElement(
          blockEditorDy,
          screen.prevLevelBlockEditor,
          screen.level - 1,
          prevBattleElapsedSeconds,
          prevBattleAnimationData
        ),
        this.renderBattleAndOutcomeScreenCanvasElement(
          blockEditorDy - 2 * appHeight,
          screen.blockEditor,
          screen.level,
          battleElapsedSeconds,
          battleAnimationData
        ),
        this.renderPuzzleScreenButtonsCanvasElement(),
      ],
    };

    return { reactElement, canvasElement };
  }

