  onCreditsButtonClick(): void {
    const animationStartTime = getCurrentTimestamp();
    const updateAnimationTimeIfAnimationIsStillInProgress = (): void => {
      const currentState = this.state;
      if (
        !(
          currentState.screen.animationKind === AnimationKind.Credits &&
          areTimestampsEqual(
            currentState.screen.animationStartTime,
            animationStartTime
          )
        )
      ) {
        return;
      }

      const currentTimestamp = getCurrentTimestamp();
      const elapsedSeconds = getElapsedSeconds(
        currentTimestamp,
        currentState.screen.animationStartTime
      );

      if (elapsedSeconds > this.getCreditRollDurationInSeconds()) {
        this.setState({
          ...currentState,
          screen: {
            ...currentState.screen,
            animationKind: AnimationKind.StaticSettings,
          },
        });
        return;
      }

      this.setState({
        ...currentState,
        screen: {
          ...currentState.screen,
          currentTimestamp,
        },
      });

      requestAnimationFrame(updateAnimationTimeIfAnimationIsStillInProgress);
    };

    this.setState(
      {
        screen: {
          ...this.state.screen,
          animationKind: AnimationKind.Credits,
          animationStartTime,
          currentTimestamp: animationStartTime,
        },
      },
      updateAnimationTimeIfAnimationIsStillInProgress
    );
  }

