  onLiteralEditorInputChange(event: React.ChangeEvent<HTMLInputElement>): void {
    this.setState((state): State => {
      const { screen } = state;
      if (screen.animationKind !== AnimationKind.Edit) {
        return state;
      }

      if (screen.blockEditor.literalEditor === null) {
        return state;
      }

      const newLiteralEditorState = {
        ...screen.blockEditor.literalEditor,
        literalSource: event.target.value,
      };

      return {
        ...state,
        screen: {
          ...screen,
          blockEditor: {
            ...screen.blockEditor,
            canvas: updateCanvasToMatchLiteralEditor(
              screen.blockEditor.canvas,
              newLiteralEditorState
            ),
            literalEditor: newLiteralEditorState,
          },
        },
      };
    });
  }

  onCloseLiteralEditorButtonClick(): void {
    this.setState((state): State => {
      const { screen } = state;
      if (screen.animationKind !== AnimationKind.Edit) {
        return state;
      }

      if (screen.blockEditor.literalEditor === null) {
        return state;
      }

      return {
        ...state,
        screen: {
          ...screen,
          blockEditor: {
            ...screen.blockEditor,
            canvas: updateCanvasToMatchLiteralEditor(
              screen.blockEditor.canvas,
              screen.blockEditor.literalEditor
            ),
            literalEditor: null,
          },
        },
      };
    });
  }

  measureSvgText(
    text: string,
    fontFamily: string,
    fontSizePx: number
  ): Dimensions {
    const ghostSvg = document.getElementById(DOM_ELEMENT_IDS.ghostSvg);
    if (ghostSvg === null) {
      throw new Error("No ghost SVG element is in the document.");
    }

    ghostSvg.setAttribute("width", String(this.state.appWidth));
    ghostSvg.setAttribute("height", String(this.state.appHeight));

    const textElement = document.createElementNS(
      "http://www.w3.org/2000/svg",
      "text"
    );
    textElement.textContent = text;
    textElement.style.fontFamily = fontFamily;
    textElement.style.fontSize = fontSizePx + "px";
    ghostSvg.appendChild(textElement);
    const bbox = textElement.getBBox();
    ghostSvg.removeChild(textElement);

    return bbox;
  }

  measureCanvasText({
    text,
    ...font
  }: { readonly text: string } & CanvasTextFont): Dimensions {
    const { tempCanvasCtx } = this;

    applyFont(font, tempCanvasCtx);
    const output = tempCanvasCtx.measureText(text);

    return {
      width: output.width,
      height: output.actualBoundingBoxAscent + output.actualBoundingBoxDescent,
    };
  }

  getMainBlockEditorConfig(): BlockEditorConfig<BlockKind> {
    const { appHeight } = this.state;
    return {
      componentInstanceUid: this.mainBlockEditorComponentInstanceUid,

      sizing: {
        ...MAIN_BLOCK_EDITOR_STATIC_CONFIG.sizingWithoutWindowDependentProperties,

        editorBounds: {
          topLeft: { x: 0, y: 0 },
          dimensions: { width: this.state.appWidth, height: appHeight },
        },
        paletteBounds: {
          topLeft: { x: 0, y: 0 },
          dimensions: { width: SIZING.paletteWidth, height: appHeight },
        },
        canvasBounds: {
          topLeft: { x: SIZING.paletteWidth, y: 0 },
          dimensions: {
            width: this.state.appWidth - SIZING.paletteWidth,
            height: appHeight,
          },
        },

        blockDropdownMenuDimensions: {
          width: 0.5 * (this.state.appWidth - SIZING.paletteWidth),
          height: 0.5 * appHeight,
        },

        dividers: [
          {
            topLeft: { x: SIZING.paletteWidth, y: 0 },
            dimensions: {
              width: 1,
              height: appHeight,
            },
          },
        ],
      },
      colors: MAIN_BLOCK_EDITOR_STATIC_CONFIG.colors,
      fonts: MAIN_BLOCK_EDITOR_STATIC_CONFIG.fonts,
      defs: getLocalizedBlockDefs(this.state.language),
    };
  }

  setBlockEditorState(
    updateBlockEditorState: (
      oldState: BlockEditorState<BlockKind>
    ) => BlockEditorState<BlockKind>,
    optionalCallback?: (newState: BlockEditorState<BlockKind>) => void
  ): void {
    let newBlockEditorState: null | BlockEditorState<BlockKind> = null;
    this.setState(
      (oldState) => {
        const oldBlockEditorState = oldState.screen.blockEditor;
        newBlockEditorState = updateBlockEditorState(oldBlockEditorState);

        if (oldState.screen.level === Level.Level1) {
          const isDraggingWhenBattleStartsBlock =
            newBlockEditorState.uiTouch !== null &&
            newBlockEditorState.uiTouch.kind === UiTouchStateKind.BlockDrag &&
            newBlockEditorState.uiTouch.draggeeOriginalAddress[0] ===
              CANVAS_ADDRESS_PART &&
            newBlockEditorState.uiTouch.draggeeOriginalAddress[1] === 0 &&
            newBlockEditorState.uiTouch.draggeeOriginalAddress[2] === 0;
          const isScrollingCanvas =
            newBlockEditorState.canvas.scrollInProgress !== null;
          const shouldPreventUpdate =
            isDraggingWhenBattleStartsBlock || isScrollingCanvas;

          if (shouldPreventUpdate) {
            newBlockEditorState = oldState.screen.blockEditor;
          }

          newBlockEditorState = {
            ...newBlockEditorState,
            canvas: {
              ...newBlockEditorState.canvas,
              items: newBlockEditorState.canvas.items.filter(
                (item) =>
                  item.content.length >= 1 &&
                  item.content[0].kind === BlockKind.WhenBattleStarts
              ),
            },
          };
        }

        const shouldResetMostRecentTutorialBlockEditorUpdateTime =
          oldState.screen.level === Level.Level1 &&
          areAnyTrue([
            oldBlockEditorState.uiTouch !== null &&
              newBlockEditorState.uiTouch === null,

            oldBlockEditorState.canvas.items.length === 1 &&
              newBlockEditorState.canvas.items.length === 1 &&
              newBlockEditorState.canvas.items[0].content.length !==
                oldBlockEditorState.canvas.items[0].content.length,
          ]);

        return {
          screen: {
            ...this.state.screen,
            blockEditor: newBlockEditorState,
            mostRecentTutorialBlockEditorUpdateTime:
              shouldResetMostRecentTutorialBlockEditorUpdateTime
                ? getCurrentTimestamp()
                : this.state.screen.mostRecentTutorialBlockEditorUpdateTime,
          },
        };
      },
      () => {
        if (newBlockEditorState === null) {
          throw new Error(
            "Expected newBlockEditorState to be set by the time ReactComponent.prototype.setState callback was called."
          );
        }

        if (optionalCallback !== undefined) {
          optionalCallback(newBlockEditorState);
        }
      }
    );
  }

