function parseBlockDefString(blockDefString: string): BlockDef<BlockKind> {
  const flags = {
    undeletable: blockDefString.includes("%%undeletable"),
    hermaphroditeCommand: blockDefString.includes("%%hermaphroditeCommand"),
    maleCommand: blockDefString.includes("%%maleCommand"),
    femaleCommand: blockDefString.includes("%%femaleCommand"),
    query: blockDefString.includes("%%query"),
    entryPoint: blockDefString.includes("%%entryPoint"),
  } as const;

  let remaining = blockDefString
    .replace(
      /%%(?:undeletable|hermaphroditeCommand|maleCommand|femaleCommand|query|entryPoint)/g,
      ""
    )
    .replace(/ {2,}/g, " ");
  const signature: CommandSignaturePart[] = [];
  const dropdownDefaults: string[] = [];
  const literalDefaults: string[] = [];

  while (remaining.length > 0) {
    const expressionMatch = remaining.match(/^\([^)]*\)/);
    if (expressionMatch !== null) {
      signature.push({
        kind: BlockSignaturePartKind.Expression,
      });

      literalDefaults.push(expressionMatch[0].slice(1, -1).trim());

      remaining = remaining.slice(expressionMatch[0].length);
      continue;
    }

    const dropdownMatch = remaining.match(/^\[[^\]]*\]/);
    if (dropdownMatch !== null) {
      const choices = dropdownMatch[0]
        .slice(1, -1)
        .split("||")
        .map((s) => s.trim());
      signature.push({
        kind: BlockSignaturePartKind.Dropdown,
        choices,
      });

      dropdownDefaults.push(choices[0]);

      remaining = remaining.slice(dropdownMatch[0].length);
      continue;
    }

    const sequenceMatch = remaining.match(/^\{\}/);
    if (sequenceMatch !== null) {
      signature.push({
        kind: BlockSignaturePartKind.CommandSequence,
      });
      remaining = remaining.slice(sequenceMatch[0].length);
      continue;
    }

    const indexOfNextPart = remaining.search(/[([{]/);
    const labelText = remaining.slice(
      0,
      indexOfNextPart === -1 ? remaining.length : indexOfNextPart
    );
    const trimmedLabelText = labelText.trim();
    if (trimmedLabelText.length > 0) {
      signature.push({
        kind: BlockSignaturePartKind.Label,
        text: trimmedLabelText,
      });
    }

    remaining = remaining.slice(labelText.length);
  }

  const defaultArgs: BlockArgs<BlockKind> = {
    expressions: literalDefaults.map((source) => ({
      isLiteral: true,
      source,
    })),
    dropdowns: dropdownDefaults,
    sequences: signature
      .filter((part) => part.kind === BlockSignaturePartKind.CommandSequence)
      .map(() => []),
  };

  if (
    flags.query &&
    !flags.hermaphroditeCommand &&
    !flags.maleCommand &&
    !flags.femaleCommand &&
    !flags.entryPoint
  ) {
    return {
      kind: BlockDefKind.Query,
      isDeletable: !flags.undeletable,
      signature: signature.filter(
        (
          part
        ): part is
          | BlockSignatureLabel
          | BlockSignatureDropdown
          | BlockSignatureExpression => {
          if (
            part.kind === BlockSignaturePartKind.Label ||
            part.kind === BlockSignaturePartKind.Dropdown ||
            part.kind === BlockSignaturePartKind.Expression
          ) {
            return true;
          }

          throw new Error(
            "Illegal block def string: " + JSON.stringify(blockDefString)
          );
        }
      ),
      defaultArgs,
    };
  }

  if (
    !flags.query &&
    [flags.hermaphroditeCommand, flags.maleCommand, flags.femaleCommand].filter(
      (x) => x
    ).length === 1
  ) {
    return {
      kind: BlockDefKind.Command,
      isDeletable: !flags.undeletable,
      isEntryPoint: flags.entryPoint,
      hasFemaleSocket: flags.femaleCommand || flags.hermaphroditeCommand,
      hasMaleSocket: flags.maleCommand || flags.hermaphroditeCommand,
      signature,
      defaultArgs,
    };
  }

  throw new Error(
    "Illegal block def string: " + JSON.stringify(blockDefString)
  );
}

function getPaletteBlockKinds(level: Level): readonly BlockKind[] {
  const dict: Record<Level, readonly BlockKind[]> = {
    [Level.Level1]: [BlockKind.MoveForward],
    [Level.Level2]: [BlockKind.MoveForward, BlockKind.Attack],
    [Level.Level3]: [BlockKind.MoveForward, BlockKind.Attack],
    [Level.Level4]: [BlockKind.MoveForward, BlockKind.Attack, BlockKind.Heal],
    [Level.Level5]: [BlockKind.MoveForward, BlockKind.Attack, BlockKind.Heal],
    [Level.Level6]: [
      BlockKind.MoveForward,
      BlockKind.Attack,
      BlockKind.Heal,
      BlockKind.MoveBackward,
    ],
    [Level.Level7]: [BlockKind.MoveForward, BlockKind.Attack],
  };
  return dict[level];
}

function getFrontAttackingWitchIndex(levelState: LevelState): number {
  const heroIndex = getHeroIndex(levelState);
  if (heroIndex === -1) {
    return -1;
  }

  const maxAttackerIndex = Math.min(
    levelState.squares.length - 1,
    heroIndex + BATTLE_CONFIG.witchAttackRange
  );
  for (let i = heroIndex + 1; i <= maxAttackerIndex; ++i) {
    const square = levelState.squares[i];
    if (square.kind === SquareKind.Witch) {
      return i;
    }

    // A witch can't attack the hero if something is between the two.
    if (square.kind !== SquareKind.Empty) {
      return -1;
    }
  }

  return -1;
}

function evaluateSimpleClampedFunction(
  f: SimpleFunction<number>,
  x: number
): number;
function evaluateSimpleClampedFunction<T>(
  f: SimpleFunction<T>,
  x: number,
  vso: VectorSpaceOperations<T>
): T;
function evaluateSimpleClampedFunction<T>(
  f: SimpleFunction<T>,
  x: number,
  optVso?: VectorSpaceOperations<T>
): T {
  const { points } = f;
  if (points.length === 0) {
    throw new Error(
      "Cannot evaluate a stepwise linear function with no points."
    );
  }

  if (optVso === undefined && typeof points[0].y !== "number") {
    throw new Error(
      "You must provide a VectorSpaceOperations argument for functions on non-number types."
    );
  }

  const vso = optVso ?? (REAL_VSO as unknown as VectorSpaceOperations<T>);

  if (points.length === 1) {
    return points[0].y;
  }

  const firstPoint = points[0];
  if (x <= firstPoint.x) {
    return firstPoint.y;
  }

  const lastPoint = points[points.length - 1];
  if (x >= lastPoint.x) {
    return lastPoint.y;
  }

  for (let i = 1; i < points.length; ++i) {
    const prevPoint = points[i - 1];
    const nextPoint = points[i];
    if (prevPoint.x <= x && x <= nextPoint.x) {
      const t = (x - prevPoint.x) / (nextPoint.x - prevPoint.x);
      return vso.add(
        prevPoint.y,
        vso.scale(vso.add(nextPoint.y, vso.scale(prevPoint.y, -1)), t)
      );
    }
  }

  throw new Error(
    "Malformed StepwiseLinearFunction. Points: " + JSON.stringify(points)
  );
}

function horizontallyStretchSimpleFunction<T, F extends SimpleFunction<T>>(
  f: F,
  stretchFactor: number
): F;
function horizontallyStretchSimpleFunction<T>(
  f: SimpleFunction<T>,
  stretchFactor: number
): SimpleFunction<T> {
  return {
    kind: "StepwiseLinearFunction",
    points: f.points.map((point) => ({
      x: point.x * stretchFactor,
      y: point.y,
    })),
  };
}

function applyElementwiseToColor32(
  c: Color32,
  f: (x: number) => number
): Color32 {
  return {
    r: f(c.r),
    g: f(c.g),
    b: f(c.b),
    a: f(c.a),
  };
}

/** Returns `later - earlier` in seconds. */
function getElapsedSeconds(later: Timestamp, earlier: Timestamp): number {
  return (later.millisecondsSinceEpoch - earlier.millisecondsSinceEpoch) * 1e-3;
}

function isScreenShowingVictory(screen: ScreenState): boolean {
  if (
    ![AnimationKind.PlayBattle, AnimationKind.PauseBattle].includes(
      screen.animationKind
    )
  ) {
    return false;
  }

  const levelState = getInitialLevelState(screen.level);
  const events = getBattleEvents(
    screen.blockEditor.canvas,
    getInitialLevelState(screen.level)
  );
  const elapsedSeconds = getElapsedSeconds(
    screen.currentTimestamp,
    screen.animationStartTime
  );
  const battleElapsedSeconds =
    screen.animationKind === AnimationKind.PlayBattle
      ? screen.battleAnimationOffsetSeconds + elapsedSeconds
      : screen.animationKind === AnimationKind.PauseBattle
      ? screen.battleAnimationOffsetSeconds
      : 0;

  if (
    !(
      screen.animationKind === AnimationKind.PlayBattle ||
      screen.animationKind === AnimationKind.PauseBattle
    )
  ) {
    return false;
  }

  const { battleStatus } = getBattleAnimationData(
    levelState,
    events,
    battleElapsedSeconds
  );
  return battleStatus === BattleStatus.Victory;
}

function isScreenShowingDefeat(screen: ScreenState): boolean {
  if (
    ![AnimationKind.PlayBattle, AnimationKind.PauseBattle].includes(
      screen.animationKind
    )
  ) {
    return false;
  }

  const levelState = getInitialLevelState(screen.level);
  const events = getBattleEvents(
    screen.blockEditor.canvas,
    getInitialLevelState(screen.level)
  );
  const elapsedSeconds = getElapsedSeconds(
    screen.currentTimestamp,
    screen.animationStartTime
  );
  const battleElapsedSeconds =
    screen.animationKind === AnimationKind.PlayBattle
      ? screen.battleAnimationOffsetSeconds + elapsedSeconds
      : screen.animationKind === AnimationKind.PauseBattle
      ? screen.battleAnimationOffsetSeconds
      : 0;

  if (
    !(
      screen.animationKind === AnimationKind.PlayBattle ||
      screen.animationKind === AnimationKind.PauseBattle
    )
  ) {
    return false;
  }

  const { battleStatus } = getBattleAnimationData(
    levelState,
    events,
    battleElapsedSeconds
  );
  return battleStatus === BattleStatus.Defeat;
}

export function typecheckedAssertNever(impossible: never): never {
  return impossible;
}

function renderRect(rect: CanvasRect): CanvasElement {
  // Based on https://github.com/jarek-foksa/path-data-polyfill/blob/master/path-data-polyfill.js
  const { x, y, w, h } = rect;
  const rx = Math.min(rect.rx ?? 0, w / 2);
  const ry = Math.min(rect.ry ?? 0, h / 2);

  var pathData = [
    { type: "M", values: [x + rx, y] },
    { type: "H", values: [x + w - rx] },
    { type: "A", values: [rx, ry, 0, 0, 1, x + w, y + ry] },
    { type: "V", values: [y + h - ry] },
    { type: "A", values: [rx, ry, 0, 0, 1, x + w - rx, y + h] },
    { type: "H", values: [x + rx] },
    { type: "A", values: [rx, ry, 0, 0, 1, x, y + h - ry] },
    { type: "V", values: [y + ry] },
    { type: "A", values: [rx, ry, 0, 0, 1, x + rx, y] },
    { type: "Z", values: [] },
  ];

  // Get rid of redundant "A" segs when either rx or ry is 0
  pathData = pathData.filter(function (s) {
    return s.type === "A" && (s.values[0] === 0 || s.values[1] === 0)
      ? false
      : true;
  });

  const path = new Path2D(
    pathData
      .map((command) => command.type + command.values.join(" "))
      .join("\n")
  );

  return {
    kind: CanvasElementKind.Path,
    path,
    stroke: rect.stroke,
    strokeWidth: rect.strokeWidth,
    fill: rect.fill,
  };
}

function renderCircle(circle: CanvasCircle): CanvasElement {
  const { cx, cy, r } = circle;
  return renderRect({
    x: cx - r,
    y: cy - r,
    w: 2 * r,
    h: 2 * r,
    rx: r,
    ry: r,
    stroke: circle.stroke,
    strokeWidth: circle.strokeWidth,
    fill: circle.fill,
  });
}

